import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {BehaviorSubject} from 'rxjs';
import {
    EventNode,
    GaugeMeasure,
    GaugeNode,
    RadarNode,
    RainComputation,
    RainComputationMap,
    RainComputationQuality,
    RainNode,
    SpeedMatrixContainer,
    TeamNode,
} from 'raain-model';

import {Storage} from './storage.service';

import {FidjStorage, FidjStorageNode} from './fidj-storage.model';
import {ErrorInterface, FidjService, LoggerLevelEnum} from 'fidj-angular';
import {XYType} from './xytype';
import {GaugeNodeFilter} from './tools';
import {Router} from '@angular/router';

@Injectable({
    providedIn: 'root',
})
export class ProfileService {
    public readyForSync: BehaviorSubject<boolean>;
    public asTeamId: string;

    protected email: string;
    protected isDemo: boolean;
    protected roles: Array<string>;
    protected nodeData: FidjStorageNode;
    protected fidjStorage: FidjStorage;
    protected haveTryToReconnect: boolean;

    constructor(
        protected storage: Storage,
        protected fidjService: FidjService,
        protected httpClient: HttpClient,
        private router: Router
    ) {
        this.email = this.storage.get('raain-email');
        this.asTeamId = this.storage.get('raain-asTeamId');
        this.readyForSync = new BehaviorSubject<boolean>(false);
        this.roles = [];
        this.fidjStorage = new FidjStorage(storage);
        this.isDemoMode = false;
    }

    get isDemoMode() {
        return this.isDemo;
    }

    set isDemoMode(mode) {
        this.isDemo = mode ? mode : true;
        this.fidjStorage.setDemoMode(this.isDemo);
    }

    get defaultUrlForAPI() {
        return this.storage.get('raain-urlForAPI');
    }

    set defaultUrlForAPI(url: string) {
        this.storage.set('raain-urlForAPI', url);
    }

    async refreshProfile(): Promise<FidjStorageNode> {
        try {
            this.nodeData = await this.fidjStorage.getRefreshedNodeCopy(this.fidjService);
            this.setRoles(await this.fidjService.getRoles());
            return this.nodeData;
        } catch (e) {
            await this.checkError(e);
        }
    }

    async isConnected() {
        return this.fidjService.isConnected();
    }

    getEmail() {
        return this.email ?? this.storage.get('raain-email', this.email);
    }

    setEmail(email: string) {
        this.email = email;
        this.storage.set('raain-email', this.email);
    }

    async logout(fidjKey: string, fidjProd: boolean) {
        // this.storage.remove('raain-email');
        await this.fidjService.logout(true);

        try {
            await this.fidjService.init(fidjKey, {
                logLevel: LoggerLevelEnum.WARN, // fidjProd ? LoggerLevelEnum.WARN : LoggerLevelEnum.INFO,
                crypto: false,
                prod: fidjProd,
                useDB: false,
            });
            this.readyForSync.next(true);
        } catch (err) {
            console.error('initFidj catch pb: ', err);
        }
    }

    async checkError(error: ErrorInterface) {
        if (error.code === 401) {
            console.warn('Pb on auth');
            if (this.router.url.indexOf('login') < 0) {
                try {
                    await this.fidjService.logout();
                } catch (ignored) {
                    // Ignore logout errors as we're redirecting to logout page anyway
                }
                return this.gotoLout();
            }
        }
    }

    async gotoLout() {
        try {
            if (this.router.url.indexOf('login') > -1) {
                return;
            }
            await this.router.navigateByUrl('/logout', {
                skipLocationChange: true,
                replaceUrl: true,
            });
        } catch (e) {
            console.error('gotoLout error: ', e);
        }
    }

    async gotoLogin() {
        if (this.router.url.indexOf('login') > -1) {
            return;
        }
        // await this.router.navigateByUrl('/', {skipLocationChange: true});
        await this.router.navigate(['/login']);
    }

    isLoggedIn() {
        const loggedIn = this.fidjService.isLoggedIn();
        console.log('isLoggedIn: ', loggedIn);
        return loggedIn;
    }

    needsConnectionRefresh() {
        return this.fidjService.needsRefresh();
    }

    async connectionRefresh() {
        await this.fidjService.refresh();
    }

    async storeAll() {
        return this.fidjStorage.storeData(this.fidjService, this.nodeData);
    }

    isAdmin(): boolean {
        return this.roles.indexOf('admin') > -1;
    }

    // === Notifications ===

    async createNotification(rainId: string, message: string) {
        try {
            const resp = await this.fidjService.sendOnEndpoint({
                key: 'notifications',
                verb: 'POST',
                defaultKeyUrl: this.defaultUrlForAPI + '/notifications',
                data: {
                    rain: rainId,
                    message,
                },
            });
            return new EventNode(resp.data);
        } catch (e) {
            await this.checkError(e);
        }

        return null;
    }

    async getNotifications(rainId: string) {
        try {
            const args = '?rain=' + rainId;
            const resp = await this.fidjService.sendOnEndpoint({
                key: 'notifications',
                verb: 'GET',
                relativePath: args,
                defaultKeyUrl: this.defaultUrlForAPI + '/notifications',
            });

            return resp.data.notifications.map((n) => new EventNode(n));
        } catch (e) {
            await this.checkError(e);
        }

        return null;
    }

    async getAllNotifications(): Promise<EventNode[]> {
        try {
            const resp = await this.fidjService.sendOnEndpoint({
                key: 'notifications',
                verb: 'GET',
                defaultKeyUrl: this.defaultUrlForAPI + '/notifications',
            });

            return resp.data.notifications.map((n) => new EventNode(n));
        } catch (e) {
            await this.checkError(e);
        }

        return [];
    }

    // === Teams ===

    async getTeams(): Promise<Array<TeamNode>> {
        const teams = [];
        try {
            const resp = await this.fidjService.sendOnEndpoint({
                key: 'teams_all',
                verb: 'GET',
                defaultKeyUrl: this.defaultUrlForAPI + '/teams_all',
            });
            for (const team of resp.data.teams) {
                teams.push(new TeamNode(team));
            }
        } catch (e) {
            await this.checkError(e);
        }
        return teams;
    }

    async getTeam(teamId: string): Promise<TeamNode> {
        try {
            const resp = await this.fidjService.sendOnEndpoint({
                key: 'teams',
                verb: 'GET',
                relativePath: teamId,
                defaultKeyUrl: this.defaultUrlForAPI + '/teams',
            });
            return new TeamNode(resp.data);
        } catch (e) {
            await this.checkError(e);
        }

        return null;
    }

    // === Radars ===

    async getRadars(teamId: string): Promise<Array<RadarNode>> {
        const radars = [];
        const url = this.defaultUrlForAPI + '/radars' + (teamId ? '?teamId=' + teamId : '');
        try {
            const resp = await this.fidjService.sendOnEndpoint({
                key: 'radars',
                verb: 'GET',
                defaultKeyUrl: url,
            });
            for (const r of resp.data.radars) {
                const radar = new RadarNode(r);
                radars.push(radar);
            }
        } catch (e) {
            await this.checkError(e);
        }
        return radars;
    }

    async getRadar(id: string): Promise<RadarNode> {
        try {
            const resp = await this.fidjService.sendOnEndpoint({
                key: 'radars',
                verb: 'GET',
                relativePath: id,
                defaultKeyUrl: this.defaultUrlForAPI + '/radars',
            });
            return new RadarNode(resp.data);
        } catch (e) {
            await this.checkError(e);
        }

        return null;
    }

    async putRadar(radarNode: RadarNode): Promise<RadarNode> {
        const data = {
            name: radarNode.name,
        };

        try {
            const resp = await this.fidjService.sendOnEndpoint({
                key: 'radars',
                relativePath: radarNode.id,
                verb: 'PUT',
                data,
                defaultKeyUrl: this.defaultUrlForAPI + '/radars/',
            });

            return new RadarNode(resp.data);
        } catch (e) {
            await this.checkError(e);
        }
    }

    async getLonelyRadars(rains: RainNode[]): Promise<RadarNode[]> {
        try {
            const resp = await this.fidjService.sendOnEndpoint({
                key: 'radars',
                verb: 'GET',
                defaultKeyUrl: this.defaultUrlForAPI + '/radars',
            });
            const lonelyRadars: RadarNode[] = [];
            const radars = resp.data.radars;
            radars.forEach((radar) => {
                let found = false;
                rains.forEach((rain) => {
                    const rdId = rain.getLink('radar').getId();
                    if (rdId === radar.id) {
                        found = true;
                    }
                });
                if (!found) {
                    lonelyRadars.push(radar);
                }
            });
            return lonelyRadars;
        } catch (e) {
            await this.checkError(e);
        }
        return [];
    }

    async getRainTimeframe(
        rainId: string,
        begin: Date,
        end: Date,
        forced = false
    ): Promise<RainNode> {
        try {
            let queryPath = '' + rainId + '?format=timeframe';
            if (begin) {
                queryPath += '&begin=' + begin.toISOString();
            }
            if (end) {
                queryPath += '&end=' + end.toISOString();
            }
            if (forced) {
                queryPath += '&forced=true';
                queryPath += '&withConfig=true';
                // queryPath += '&bypassCache=' + new Date().getTime();
            }
            const resp = await this.fidjService.sendOnEndpoint({
                key: 'rains',
                verb: 'GET',
                relativePath: queryPath,
                defaultKeyUrl: this.defaultUrlForAPI + '/rains',
            });
            const rainNode = new RainNode(resp.data.timeframe);
            rainNode.name += '.radar.timeframe';
            return rainNode;
        } catch (e) {
            await this.checkError(e);
        }
        return null;
    }

    // === Rains ===

    async getRains(teamId: string): Promise<Array<RainNode>> {
        const rains = [];
        const url = this.defaultUrlForAPI + '/rains' + (teamId ? '?teamId=' + teamId : '');
        try {
            const resp = await this.fidjService.sendOnEndpoint({
                key: 'rains',
                verb: 'GET',
                defaultKeyUrl: url,
            });
            for (const rain of resp.data.rains) {
                rains.push(new RainNode(rain));
            }
        } catch (e) {
            await this.checkError(e);
        }
        return rains;
    }

    async getRain(id: string): Promise<RainNode> {
        try {
            const resp = await this.fidjService.sendOnEndpoint({
                key: 'rains',
                relativePath: id,
                verb: 'GET',
                defaultKeyUrl: this.defaultUrlForAPI + '/rains/',
            });
            return new RainNode(resp.data);
        } catch (e) {
            await this.checkError(e);
        }
        return null;
    }

    // === Count

    async getCounts(
        rainId: string,
        options: {
            range: 'hour' | 'day' | 'month' | 'year';
            periodBegin: Date;
        }
    ): Promise<{
        percentImages: XYType[];
        percentRainy: XYType[];
        percentQ: XYType[];
        queueRunning: number;
    }> {
        try {
            const args = `?range=${options.range}&begin=${options.periodBegin.toISOString()}`;

            const resp = await this.fidjService.sendOnEndpoint({
                key: 'rains',
                relativePath: rainId + '/counts' + args,
                verb: 'GET',
                defaultKeyUrl: this.defaultUrlForAPI + '/rains/',
            });

            const counts = resp.data.counts.result;
            const percentImages: XYType[] = [],
                percentRainy: XYType[] = [],
                percentQ: XYType[] = [];

            counts.forEach((c) => {
                const label = this.setDateComponents(options.periodBegin, c);
                percentImages.push(new XYType(c.percentImages ?? 0, NaN, NaN, label));
                percentRainy.push(new XYType(c.percentRainy ?? 0, NaN, NaN, label));
                percentQ.push(new XYType(c.percentQ ?? 0, NaN, NaN, label));
            });

            return {
                percentImages,
                percentRainy,
                percentQ,
                queueRunning: resp.data.queueRunning,
            };
        } catch (e) {
            await this.checkError(e);
        }
        return null;
    }

    async getCountsHour(
        rainId: string,
        options: {periodBegin: Date}
    ): Promise<{
        percentImages: XYType[];
        rainyCount: XYType[];
        rainySum: XYType[];
        queueRunning: number;
    }> {
        try {
            const args = `?range=hour&begin=${options.periodBegin.toISOString()}`;

            const resp = await this.fidjService.sendOnEndpoint({
                key: 'rains',
                relativePath: rainId + '/counts' + args,
                verb: 'GET',
                defaultKeyUrl: this.defaultUrlForAPI + '/rains/',
            });

            const counts = resp.data.counts.result;
            const percentImages: XYType[] = [],
                rainySum: XYType[] = [],
                rainyCount: XYType[] = [];

            counts.forEach((c) => {
                const label = this.setDateComponents(options.periodBegin, c);
                percentImages.push(new XYType(c.percentImages ?? 0, NaN, NaN, label));
                rainyCount.push(new XYType(c.rainyCount ?? 0, NaN, NaN, label));
                rainySum.push(new XYType(c.rainySum ?? 0, NaN, NaN, label));
            });

            return {
                percentImages,
                rainyCount,
                rainySum,
                queueRunning: resp.data.queueRunning,
            };
        } catch (e) {
            await this.checkError(e);
        }
        return null;
    }

    // === Computing ===

    async launchRainComputation(rainId: string, date: Date): Promise<RainComputation[]> {
        try {
            const response = await this.fidjService.sendOnEndpoint({
                verb: 'POST',
                key: 'rains',
                relativePath: '' + rainId + '/computations',
                data: {
                    date: date.toISOString(),
                },
                defaultKeyUrl: this.defaultUrlForAPI + '/rains',
            });

            return response.data.computations?.map((c) => new RainComputation(c));
        } catch (e) {
            await this.checkError(e);
        }
        return null;
    }

    async getRainComputationCartesianMapById(
        rainId: string,
        rainComputationId: string
    ): Promise<RainComputationMap> {
        try {
            const queryPath =
                '' + rainId + '/computations/' + rainComputationId + '?format=cartesian-map';
            const response = await this.fidjService.sendOnEndpoint({
                key: 'rains',
                verb: 'GET',
                relativePath: queryPath,
                defaultKeyUrl: this.defaultUrlForAPI + '/rains',
            });
            if (!response.data['cartesian-map']) {
                return null;
            }

            const rainComputationMap = new RainComputationMap(response.data['cartesian-map']);
            rainComputationMap.name = rainId + '.rain.cartesian-map';
            return rainComputationMap;
        } catch (e) {
            await this.checkError(e);
        }
        return null;
    }

    async getRainComputationCumulativeMapById(
        rainId: string,
        rainComputationId: string,
        cumulativeHours: number
    ): Promise<RainComputationMap> {
        const queryPath =
            '' + rainId + '/computations/' + rainComputationId + '/cumulative/' + cumulativeHours;
        const response = await this.fidjService.sendOnEndpoint({
            key: 'rains',
            verb: 'GET',
            relativePath: queryPath,
            defaultKeyUrl: this.defaultUrlForAPI + '/rains',
        });
        if (!response.data['cumulative']) {
            return null;
        }

        const rainComputationMap = new RainComputationMap(response.data['cumulative']);
        rainComputationMap.name = rainId + '.rain.cumulative';
        return rainComputationMap;
    }

    async getRainCompares(
        rainId: string,
        rainComputationId: string
    ): Promise<{id: string; version: string}[]> {
        const queryPath = '' + rainId + '/computations/' + rainComputationId;
        const response = await this.fidjService.sendOnEndpoint({
            key: 'rains',
            verb: 'GET',
            relativePath: queryPath,
            defaultKeyUrl: this.defaultUrlForAPI + '/rains',
            // timeout: 200000,
        });

        const rainComputation = new RainComputation(response.data);
        const links = rainComputation.getLinks();
        const rainComputationQualities = [];
        for (const link of links) {
            if (link.rel === RainComputationQuality.TYPE) {
                const id = link.getId();
                const dateLength = new Date().toISOString().length;
                const version = link.href.substring(
                    4 + RainComputationQuality.TYPE.length + 1 + dateLength + 1,
                    link.href.indexOf('/' + id)
                );
                rainComputationQualities.push({id, version});
            }
        }
        return rainComputationQualities;
    }

    async getRainCompareByDate(
        rainId: string,
        rainComputationId: string,
        date: Date
    ): Promise<RainComputationQuality> {
        const queryPath =
            '' +
            rainId +
            '/computations/' +
            rainComputationId +
            '/compares?date=' +
            date.toISOString();

        try {
            const response = await this.fidjService.sendOnEndpoint({
                key: 'rains',
                verb: 'GET',
                relativePath: queryPath,
                defaultKeyUrl: this.defaultUrlForAPI + '/rains',
            });

            const rainComputationQuality = new RainComputationQuality(response.data.qualities[0]);
            rainComputationQuality.qualitySpeedMatrixContainer =
                SpeedMatrixContainer.CreateFromJson(
                    rainComputationQuality.qualitySpeedMatrixContainer
                );
            return rainComputationQuality;
        } catch (e) {
            await this.checkError(e);
        }
        return null;
    }

    async getRainProgress(rainId: string): Promise<number> {
        try {
            const queryPath = '' + rainId + '/progress';
            const response = await this.fidjService.sendOnEndpoint({
                key: 'rains',
                verb: 'GET',
                relativePath: queryPath,
                defaultKeyUrl: this.defaultUrlForAPI + '/rains',
                // timeout: 200000,
            });

            return response.data.inProgress;
        } catch (e) {
            await this.checkError(e);
        }
        return 0;
    }

    // === Gauges ===

    async getGauge(gaugeId: string): Promise<GaugeNode> {
        try {
            const resp = await this.fidjService.sendOnEndpoint({
                key: 'gauges',
                verb: 'GET',
                relativePath: gaugeId,
                defaultKeyUrl: this.defaultUrlForAPI + '/gauges',
            });
            return new GaugeNode(resp.data);
        } catch (e) {
            await this.checkError(e);
        }
    }

    async getGauges(rainId?: string): Promise<Array<GaugeNodeFilter>> {
        let queryPath = '';
        if (this.asTeamId) {
            queryPath += '?teamId=' + this.asTeamId;
        }
        if (rainId) {
            if (queryPath) {
                queryPath += '&';
            } else {
                queryPath += '?';
            }
            queryPath += 'rainId=' + rainId;
        }

        const gauges = [];
        try {
            const resp = await this.fidjService.sendOnEndpoint({
                key: 'gauges',
                verb: 'GET',
                relativePath: queryPath,
                defaultKeyUrl: this.defaultUrlForAPI + '/gauges',
            });

            for (const gauge of resp.data.gauges) {
                gauges.push(new GaugeNodeFilter(gauge));
            }
        } catch (e) {
            await this.checkError(e);
        }
        return gauges;
    }

    async getGaugeMeasures(gaugeId: string, begin: Date, end: Date): Promise<GaugeMeasure[]> {
        const queryPath =
            '' + gaugeId + '/measures?begin=' + begin.toISOString() + '&end=' + end.toISOString();
        const resp = await this.fidjService.sendOnEndpoint({
            key: 'gauges',
            verb: 'GET',
            relativePath: queryPath,
            defaultKeyUrl: this.defaultUrlForAPI + '/gauges',
        });

        const gaugeMeasures: GaugeMeasure[] = [];
        for (const gaugeMeasure of resp.data.gaugeMeasures) {
            gaugeMeasures.push(new GaugeMeasure(gaugeMeasure));
        }
        return gaugeMeasures;
    }

    // === Private ===

    protected setRoles(roles: Array<string>): void {
        this.roles = roles;
    }

    private setDateComponents(
        date: Date,
        c: {
            year?: number;
            month?: number;
            day?: number;
            hour?: number;
            minute?: number;
        }
    ): string {
        const dateToShow = new Date(date);

        if (c.year !== undefined) {
            dateToShow.setUTCFullYear(c.year);
        }
        if (c.month !== undefined) {
            dateToShow.setUTCMonth(c.month - 1);
        }
        if (c.day !== undefined) {
            dateToShow.setUTCDate(c.day);
        }
        if (c.hour !== undefined) {
            dateToShow.setUTCHours(c.hour);
        }
        if (c.minute !== undefined) {
            dateToShow.setUTCMinutes(c.minute);
        }

        return dateToShow.toISOString();
    }
}
